<template>
	<div>
		<template v-if="action == 'default'">
			<Alert type="warning" show-icon v-if="client.is_init_pay != 1">
		       您的系统还没有开通，请点击下面的“立即开通”按钮进行开通操作
		    </Alert>
			<Form :label-width="100">
				<Form-item label="开通状态：">
					<template v-if="client.is_init_pay == 1">
						<Tag color="green">开通</Tag> 
						<Button type="ghost" size="small" @click="gosystem" icon="log-in">前往登录</Button>
					</template>
					<template v-else>
						<Tag color="red">待开通</Tag>
						<Button @click="init_pay" type="primary" icon="unlocked" size="small">立即开通</Button>
					</template>
				</Form-item>
				<Form-item label="机构名称：">
					{{client.client_name}}
				</Form-item>
				<Form-item label="入驻日期：">
					{{client.create_time|short_date}}
					<label style="margin-left: 30px;">到期时间：</label>
					{{client.expire_day|int_date}}
				</Form-item>	
				<Form-item label="账号容量：">
					<Progress :percent="user_used_percent" :stroke-width="15">{{client.account_num_current}}/{{client.account_num_limit}}</Progress>
				</Form-item>	
				<Form-item label="联系人：">
					{{client.contact}}
				</Form-item>
				<Form-item label="联系方式：">
					{{client.tel}}
				</Form-item>	
				<Form-item label="学校地址：">
					{{client.address}}
				</Form-item>	
			</Form>
			
			<Card style="width:160px">
				<div class="card-item">
					<div class="avatar">
						<img :src="client.saleman.user.avatar">				
					</div>
					<p class="mt-2">
						业务经理
						<span class="ml-3">
							{{client.saleman.name}}
						</span>
					</p>
					<p>{{client.saleman.mobile}}</p>
				</div>
			</Card>
		</template>
		<template v-if="action == 'success'">
			<Alert type="success" show-icon>
		        开通成功
		        <div slot="desc">
		        	<p>您已经完成缴费，并成功开通系统，您现在可以正常的使用系统，请使用您的管理员账号进行登录，并完成系统初始化设置工作。</p>
		        	<Button type="ghost" size="small" @click="gosystem" icon="log-in">前往登录</Button> </div>
		    </Alert>
		</template>
		<template v-if="action == 'init_pay'">
			<Form :label-width="100">
				<Form-item label="机构名称：">
					{{client.client_name}}
				</Form-item>
				<Form-item label="账号数：">
					{{client.account_num_limit}}
				</Form-item>	
				<Form-item label="需缴费金额：">
					<span class="amount">￥{{client.init_amount}}</span>
				</Form-item>	
			</Form>

			<div class="rel-box" v-if="loading">
		        <Spin fix>正在加载付款方式，请稍等...</Spin>
		    </div>
		    <template v-else>
		    	<h2 class="m-b">
		            <Icon type="qr-scanner"></Icon>
		            <strong>请选择任意一种方式付款</strong>
		        </h2>
		        <Row>
		        	<Col span="12" v-if="paycode && paycode.wxpay_qr_url != ''">
		        		<div class="pay-code">
		        			<img class="t" src="../img/pay_wxpay.png">
		        			<img class="qr" :src="'/vipapi/open/qrcode?uri='+paycode.wxpay_qr_url">
		        			<p>请直接用微信扫码付款</p>
		        		</div>
		        	</Col>
		        	<Col span="12" v-if="paycode && paycode.alipay_qr_url != ''">
		        		<div class="pay-code">
		        			<img class="t" src="../img/pay_alipay.png">
		        			<img class="qr" :src="'/vipapi/open/qrcode?uri='+paycode.wxpay_qr_url">
		        		</div>
		        	</Col>
		        	<Col span="12" v-if="paycode && paycode.alipay_html != ''">
		        		<div class="pay-code">
		        			<img class="t" src="../img/pay_alipay.png">
		        			<img class="qr border" src="../img/alipay.png">
							<Button class="pay-btn" type="primary" @click="goalipay">请点击付款</Button>
		        		</div>
		        		<div class="pay-form" v-html="paycode.alipay_html">
		        		</div>
		        	</Col>
		        </Row>
		    </template>

		</template>
	</div>
</template>

<script>
import common from '@/libs/common.mixin'

let CLEAR_TIMEOUT = false
export default {
	mixins:[common],
	data(){
		return {
			action:'default',
			paycode:null,
			is_pay:false,
			th:null,
			loading:false
		}
	},
	computed:{
		user(){
			return this.$store.state.user
		},
		client(){
			return this.$store.state.client
		},
		user_used_percent(){
			return this.client.account_num_current*100 / this.client.account_num_limit;
		}	
	},
	beforeDestroy(){
		CLEAR_TIMEOUT = true
		
	},
	methods:{
		init_pay(){
			this.action = 'init_pay'
			this.loading = true
			this.$http.get('paycode/initpay')
			.then(response=>{
				this.loading = false
				this.paycode = response.body.data
				CLEAR_TIMEOUT = false
				this.check_order()
			},response=>{
				this.loading = false
			})

			
		},
		check_order(){
			
			if(this.is_pay || CLEAR_TIMEOUT){
				return;
			}
			this.$rest('orders').add_url_param(this.paycode.vo_id)
			.get()
			.success(data=>{
			
				if(data.pay_status == 1){
					this.is_pay = true
					this.$store.commit('setClientInitPaied')
					this.success()
				}else{
					this.th = setTimeout(()=>{
						this.check_order()
					},1000)
					
				}
			})
			.error(response=>{
				this.th = setTimeout(()=>{
					this.check_order()
				},1000)
				
			})

		},
		success(){
			this.action = 'success'
		},
		gosystem(){
			let product_url = this.$store.state.user.info.product_url
			let arr_url = product_url.split('://')
			let url = arr_url[0]+'://'+this.$store.state.client.host+'.'+arr_url[1]
			location.href = url
		},
		goalipay(){
			document.forms['alipayform'].submit()
		}
	}
}
</script>

<style lang="less">
	.rel-box{
		min-height:300px;
		position:relative;
	}
	.border{
		border:1px solid #ccc;
	}
	.m-b{
		margin-bottom:15px;
	}
	.mt-2{
		margin-top: .5em;
	}
	.card-item{
		text-align: center;
		p{
			line-height: 2em;
		}
	}
	.avatar{
		width: 80px;
		height: 80px;
		margin: 0 auto;
		img{
			width: 100%;
			height: 100%;
			border-radius: 50%;
		}
	}
	.ivu-form-item{
		margin-bottom:10px !important;
	}
	span.amount{
		font-size:18px;
		font-family:Arial;
		color:#f60;
	}
	.pay-form{
		display:none;
	}
	.pay-code{
		.qr{
			width:200px;
			display:block;
		}
		p{
			padding-left:10px;
		}
		.pay-btn{
			margin-left:30px;
			margin-top:10px;
		}
	}
</style>


